'use strict';

// MAIN //

var CTORS = [
	[ TypeError, 'TypeError' ],
	[ SyntaxError, 'SyntaxError' ],
	[ ReferenceError, 'ReferenceError' ],
	[ RangeError, 'RangeError' ],
	[ URIError, 'URIError' ],
	[ EvalError, 'EvalError' ],
	[ Error, 'Error' ]
];


// EXPORTS //

module.exports = CTORS;
